import { call, delay, put, takeLatest } from "redux-saga/effects";
import { cyberbugsService } from "../../../services/CyberbugsService";
import { STATUS_CODE } from "../../../util/constants/settingSystem";
import { DISPLAY_LOADING, HIDE_LOADING } from "../../constants/LoadingConst";
import { history } from '../../../util/history';
import { projectService } from "../../../services/ProjectService";
import { notifiFunction } from "../../../util/Notification/notificationCyberbugs";
import { GET_ALL_PROJECT, GET_ALL_PROJECT_SAGA } from "../../constants/Cyberbugs/ProjectCyberBugsConstants";
import { GET_USER_BY_PROJECT_ID_SAGA } from "../../constants/Cyberbugs/UserConstants";
function* createProjectSaga(action) {

    console.log('actionCreateProject', action)
    //HIN TH LOADING
    yield put({
        type: DISPLAY_LOADING
    })
    yield delay(500);

    try {

        //Gi api ly d liu v
        const { data, status } = yield call(() => cyberbugsService.createProjectAuthorization(action.newProject));
        //Gi api thnh cng th dispatch ln reducer thng qua put
        if (status === STATUS_CODE.SUCCESS) {
            console.log(data)

            history.push('/projectmanagement');
        }


    } catch (err) {
        console.log(err);
    }

    yield put({
        type: HIDE_LOADING
    })
}





export function* theoDoiCreateProjectSaga() {
    yield takeLatest('CREATE_PROJECT_SAGA', createProjectSaga);
}




//Saga dng  get all project t api 
//Khi - Code ngy dd/MM/yyyy

function* getListProjectSaga(action) {

    try {
        const { data, status } = yield call(() => cyberbugsService.getListProject());

        //Sau khi ly d liu t api v thnh cng
        if (status === STATUS_CODE.SUCCESS) {
            yield put({
                type: 'GET_LIST_PROJECT',
                projectList: data.content
            })
        }
    } catch (err) {
        console.log(err)
    }

}

export function* theoDoiGetListProjectSaga() {
    yield takeLatest('GET_LIST_PROJECT_SAGA', getListProjectSaga);
}

//UpdateProject
function* updateProjectSaga(action) {
    // console.log('action123',action);
    // return;
    //HIN TH LOADING
    yield put({
        type: DISPLAY_LOADING
    })
    yield delay(500);

    try {


        const { data, status } = yield call(() => cyberbugsService.updateProject(action.prjectUpdate));
        //Gi api thnh cng th dispatch ln reducer thng qua put
        if (status === STATUS_CODE.SUCCESS) {
            console.log(data)

            // history.push('/projectmanagement');
        }
        // yield put({
        //     type:'GET_LIST_PROJECT_SAGA'
        // })
        yield call(getListProjectSaga);
        yield put({
            type: 'CLOSE_DRAWER'
        })
    } catch (err) {
        console.log(err);
    }

    yield put({
        type: HIDE_LOADING
    })
}


export function* theoDoiUpdateProjectSaga() {
    yield takeLatest('UPDATE_PROJECT_SAGA', updateProjectSaga);
}

function* deleteProjectSaga(action) {
    // console.log('action123',action);
    // return;
    //HIN TH LOADING
    yield put({
        type: DISPLAY_LOADING
    })
    yield delay(500);

    try {
        const { data, status } = yield call(() => projectService.deleteProject(action.idProject));
        //Gi api thnh cng th dispatch ln reducer thng qua put
        if (status === STATUS_CODE.SUCCESS) {
            console.log(data)

            notifiFunction('success', 'Delete project successfully !')

            // history.push('/projectmanagement');
        } else {
            notifiFunction('error', 'Delete project fail !')
        }
        // yield put({
        //     type:'GET_LIST_PROJECT_SAGA'
        // })
        yield call(getListProjectSaga);
        yield put({
            type: 'CLOSE_DRAWER'
        })
    } catch (err) {
        notifiFunction('error', 'Delete project fail !')
        console.log(err);
    }

    yield put({
        type: HIDE_LOADING
    })
}


export function* theoDoiDeleteProject() {
    yield takeLatest('DELETE_PROJECT_SAGA', deleteProjectSaga);
}

function* getProjectDetailSaga(action) {
   
    yield delay(500);
    try {
        
        const { data, status } = yield call(() => projectService.getProjectDetail(action.projectId));
        console.log(data);
        // Ly d liu thnh cng
        yield put({
            type: 'PUT_PROJECT_DETAIL',
            projectDetail: data.content
        })

    } catch (err) {
        console.log('404 not found!');
        history.push('/projectmanagement');
    }
}


export function* theoDoiGetProjectDetail() {
    yield takeLatest('GET_PROJECT_DETAIL', getProjectDetailSaga);
}



function* getProjectAllSaga(action) {
    yield put({
        type: DISPLAY_LOADING
    })
    yield delay(500);

    try {
        const { data, status } = yield call(() => projectService.getAllProject());

        yield put({
            type: GET_ALL_PROJECT,
            arrProject: data.content
        })

        yield put({
            type: GET_USER_BY_PROJECT_ID_SAGA,
            idProject: data.content[0].id
        })

    } catch (err) {
        console.log('404 not found!')
        history.push('/projectmanagement');
    }
    yield put({
        type: HIDE_LOADING
    })
}

export function* theoDoiGetAllProjectSaga() {
    yield takeLatest(GET_ALL_PROJECT_SAGA, getProjectAllSaga);
}
